      min-height: 3rem;
      padding-left: 0;
      padding-right: 0; } }

.archive__facets__discipline,
.archive__facets__type {
  margin-bottom: 1rem;
  padding: 0 1rem; }
  @media screen and (min-width: 60em) {
    .archive__facets__discipline,
    .archive__facets__type {
      margin-bottom: 0;
      padding: 0; } }

@media screen and (min-width: 60em) {
  .archive__facets__type {
    width: 41.66667%;
    float: left;
    padding-left: 1rem;
    padding-right: 1rem; } }
  @media screen and (min-width: 60em) and (min-width: 40em) {
    .archive__facets__type {
      padding-left: 1rem;
      padding-right: 1rem; } }

.archive__facets__discipline select {
