  padding: 0.25rem 0.5rem;
  font-size: 80%;
  line-height: 1.5; }

.archive__facets__discipline__label {
  display: inline-block; }

div.facetwp-facet-member_type,
div.facetwp-facet-discipline {
  display: inline-block; }

div.facetwp-radio {
  background-image: none;
