div.facetwp-radio[data-value=adviser] {
  color: #007586; }
  div.facetwp-radio[data-value=adviser].checked {
    background-color: #007586;
    color: #fefefe; }

.archive__facets__search__toggler p {
  float: left;
  margin-right: 1rem; }

#facets-map svg.section__edge--bottom,
#facets-map svg.section__edge--top {
  z-index: 800;
  display: block; }

#facets-map svg.section__edge--bottom {
  top: 19px; }

#facets-grid {
  margin-top: 2rem;
  margin-bottom: 1rem; }
  #facets-grid .section-inner {
    padding: 0; }

/*********************
FOOTER STYLES
*********************/
.story__footer,
.collaboration__footer {
  clear: both;
  margin: 0 auto; }

.story__footer__section {
  margin-top: 0; }

.article-share-cta {
  padding: 1rem; }
  .article-share-cta .section-inner {
    padding-top: 0; }

.share-cta-header {
  margin: 0 auto 2rem;
  text-align: center; }

.share-cta-paragraph {
