.page__footer__widgets {
  background-color: #E51145; }

.page__footer__copyright {
  padding: 1rem;
  max-width: 73.125rem;
  margin-left: auto;
  margin-right: auto; }
  .page__footer__copyright::before, .page__footer__copyright::after {
    content: ' ';
    display: table; }
  .page__footer__copyright::after {
    clear: both; }
  .page__footer__copyright p {
    margin-bottom: 0;
    text-align: center; }

.page__footer__widgets-inner {
  max-width: 73.125rem;
  margin-left: auto;
  margin-right: auto;
  padding: 1rem 0; }
  .page__footer__widgets-inner::before, .page__footer__widgets-inner::after {
    content: ' ';
    display: table; }
  .page__footer__widgets-inner::after {
    clear: both; }
  .page__footer__widgets-inner p {
    font-weight: 600; }

.footer__widget-wrapper {
  color: #fefefe;
  width: 100%;
  float: left;
  padding-left: 1rem;
  padding-right: 1rem;
  margin-bottom: 2rem; }
  @media screen and (min-width: 40em) {
    .footer__widget-wrapper {
      padding-left: 1rem;
      padding-right: 1rem; } }
  @media screen and (min-width: 40em) {
    .footer__widget-wrapper {
      margin-bottom: 0;
      width: 50%;
      float: left;
      padding-left: 1rem;
      padding-right: 1rem;
      min-height: 8em; } }
  @media screen and (min-width: 40em) and (min-width: 40em) {
    .footer__widget-wrapper {
      padding-left: 1rem;
      padding-right: 1rem; } }
  @media screen and (min-width: 40em) {
      .footer__widget-wrapper:last-child {
        margin-bottom: 2rem; } }
  @media screen and (min-width: 60em) {
    .footer__widget-wrapper {
      width: 25%;
      float: left;
      padding-left: 1rem;
      padding-right: 1rem;
      min-height: 4em;
      margin-bottom: 0; } }
  @media screen and (min-width: 60em) and (min-width: 40em) {
    .footer__widget-wrapper {
      padding-left: 1rem;
      padding-right: 1rem; } }
  @media screen and (min-width: 60em) {
      .footer__widget-wrapper:last-child {
        margin-bottom: 0; } }

.footer__widget__title {
  font-size: 120%;
  text-transform: uppercase;
  margin-bottom: 0.5em; }

input.search-submit {
  display: inline-block;
  text-align: center;
  line-height: 1;
  cursor: pointer;
  -webkit-appearance: none;
  transition: background-color 0.25s ease-out, color 0.25s ease-out;
  vertical-align: middle;
  border: 1px solid transparent;
  border-radius: 0;
  padding: 0.85em 1em;
  margin: 0 0 1rem 0;
  font-size: 0.9rem;
  background-color: transparent;
  color: #4c4d53;
  border: 0;
  box-shadow: inset 1px 1px 0 0 #E51145, inset -1px -1px 0 0 #E51145;
  display: inline;
  position: relative;
  top: 0.45em;
  font-weight: 600; }
  [data-whatinput='mouse'] input.search-submit {
    outline: 0; }
  input.search-submit:hover, input.search-submit:focus {
    background-color: transparent;
    color: #4c4d53; }
  input.search-submit:hover {
    background-color: #E51145;
    color: #fefefe; }

.widget_search {
  line-height: 1.5em; }
  .widget_search label {
    height: auto;
    display: block; }
  .widget_search .screen-reader-text {
    display: none; }
  .widget_search input.search-field {
    border-color: #E51145;
    outline-color: #E51145;
    width: 10em;
    height: 2.6em;
    display: inline;
    padding: 0.675em; }
  .widget_search input.search-submit {
    display: inline-block;
    box-shadow: inset 1px 1px 0 0 #fefefe, inset -1px -1px 0 0 #fefefe;
    color: #fefefe; }
    .widget_search input.search-submit:hover {
      background-color: #fefefe;
      color: #343438; }

